import { useState, useEffect } from "react"
import axios from "axios"

const RightSide = ({currentWyr,option2, setWyrNumber, setRightStat, rightStat, setLeftStat, count, setCountLeft, setCountRight}) => {

    let percentageWhoVotedLeft
    let percentageWhoVotedRight
    let leftCount
    let rightCount


    useEffect(() => {
        if(rightStat !== null){
            const interval = setInterval(() => {
                if(count < rightStat){
                    setCountRight((previousCount) => previousCount + 1)
                }
            }, 20)
    
    
            return () => {
                clearInterval(interval)
            }
        }

    },[count, rightStat])


    const handleClick = async () => {
        //quick mafs
        if(currentWyr.wyr_option1_clicks === 0 && currentWyr.wyr_option2_clicks !== null){
            rightCount = currentWyr.wyr_option2_clicks + 1
            leftCount = 0

            let obj = {
                wyr_id: currentWyr.wyr_id,
                wyr_option1_clicks: leftCount,
                wyr_option2_clicks: rightCount
            }

            try {
                await axios.put('https://localhost:3001/add_click', obj)
            }
            catch(err){
                console.log(`Error adding click: ${err}`)
            }
        }

        else if(currentWyr.wyr_option1_clicks !== null && currentWyr.wyr_option2_clicks === 0){
            rightCount = 1
            leftCount = currentWyr.wyr_option1_clicks

            let obj = {
                wyr_id: currentWyr.wyr_id,
                wyr_option1_clicks: leftCount,
                wyr_option2_clicks: rightCount
            }

            try {
                await axios.put('https://localhost:3001/add_click', obj)
            }
            catch(err){
                console.log(`Error adding click: ${err}`)
            }
        }

        else if(currentWyr.wyr_option1_clicks === null && currentWyr.wyr_option2_clicks === null){
            rightCount = 1
            leftCount = 0

            let obj = {
                wyr_id: currentWyr.wyr_id,
                wyr_option1_clicks: leftCount,
                wyr_option2_clicks: rightCount
            }

            try {
                await axios.post('https://localhost:3001/add_click', obj)
            }
            catch(err){
                console.log(`Error adding click: ${err}`)
            }
        }
        else if (currentWyr.wyr_option1_clicks !== null && currentWyr.wyr_option2_clicks !== null){
            rightCount = currentWyr.wyr_option2_clicks + 1
            leftCount = currentWyr.wyr_option1_clicks

            let obj = {
                wyr_id: currentWyr.wyr_id,
                wyr_option1_clicks: leftCount,
                wyr_option2_clicks: rightCount
            }

            try {
                await axios.put('https://localhost:3001/add_click', obj)
            }
            catch(err){
                console.log(`Error adding click: ${err}`)
            }
        }

        percentageWhoVotedRight = Math.round((rightCount / (rightCount + leftCount)) * 100)
        percentageWhoVotedLeft = Math.round(100 - percentageWhoVotedRight)
        
        setLeftStat(percentageWhoVotedLeft)
        setRightStat(percentageWhoVotedRight)

        setTimeout(() => {
            setLeftStat(null)
            setRightStat(null)
            setCountLeft(0)
            setCountRight(0)
        }, 4000)
        setWyrNumber((a) => a + 1)
    }

    return ( 
        <div>
            <div className="right-side-container" onClick={handleClick}>
                {rightStat === null ? <div className="right-option">
                    {option2}
                </div> :
                <div className="right-option-container-nh">
                    <div className="right-option-nh">
                    {option2}
                    </div>
                    <p className="stat2">{`${count}% `}</p>
                </div>}
            </div>
        </div>
     );
}
 
export default RightSide;