import { useEffect, useState } from "react"
import axios from "axios"

const LeftSide = ({currentWyr, option1, setWyrNumber,setLeftStat, leftStat, setRightStat, setCountLeft, setCountRight, count}) => {

    let percentageWhoVotedLeft
    let percentageWhoVotedRight
    let leftCount
    let rightCount

    

    useEffect(() => {
        if(leftStat !== null){
            const interval = setInterval(() => {
                if(count < leftStat){
                    setCountLeft((previousCount) => previousCount + 1)
                }
            }, 20)

            return () => {
                clearInterval(interval)
            }
    
        }
    },[count, leftStat])



    const handleClick = async () => {
        //quick mafs
        if(currentWyr.wyr_option1_clicks === 0 && currentWyr.wyr_option2_clicks !== null){
            rightCount = currentWyr.wyr_option2_clicks
            leftCount = 1

            let obj = {
                wyr_id: currentWyr.wyr_id,
                wyr_option1_clicks: leftCount,
                wyr_option2_clicks: rightCount
            }

            try {
                await axios.put('https://localhost:3001/add_click', obj)
            }
            catch(err){
                console.log(`Error adding click: ${err}`)
            }

        }

        else if(currentWyr.wyr_option1_clicks !== null && currentWyr.wyr_option2_clicks === 0){
            rightCount = 0
            leftCount = currentWyr.wyr_option1_clicks + 1

            let obj = {
                wyr_id: currentWyr.wyr_id,
                wyr_option1_clicks: leftCount,
                wyr_option2_clicks: rightCount
            }

            try {
                await axios.put('https://localhost:3001/add_click', obj)
            }
            catch(err){
                console.log(`Error adding click: ${err}`)
            }
        }

        else if(currentWyr.wyr_option1_clicks === null && currentWyr.wyr_option2_clicks === null){
            rightCount = 0
            leftCount = 1

            let obj = {
                wyr_id: currentWyr.wyr_id,
                wyr_option1_clicks: leftCount,
                wyr_option2_clicks: rightCount
            }

            try {
                await axios.post('https://localhost:3001/add_click', obj)
            }
            catch(err){
                console.log(`Error adding click: ${err}`)
            }

        }
        else if (currentWyr.wyr_option1_clicks !== null && currentWyr.wyr_option2_clicks !== null){
            rightCount = currentWyr.wyr_option2_clicks
            leftCount = currentWyr.wyr_option1_clicks + 1

            let obj = {
                wyr_id: currentWyr.wyr_id,
                wyr_option1_clicks: leftCount,
                wyr_option2_clicks: rightCount
            }

            try {
                await axios.put('https://localhost:3001/add_click', obj)
            }
            catch(err){
                console.log(`Error adding click: ${err}`)
            }
        }

        percentageWhoVotedLeft = Math.round((leftCount / (leftCount + rightCount)) * 100)
        percentageWhoVotedRight = Math.round(100 - percentageWhoVotedLeft)
        setLeftStat(percentageWhoVotedLeft)
        setRightStat(percentageWhoVotedRight)

    
        setTimeout(() => {
            setLeftStat(null)
            setRightStat(null)
            setCountLeft(0)
            setCountRight(0)
        }, 4000)
        setWyrNumber((a) => a + 1)
    }

    return ( 
        <div>
            <div className="left-side-container" onClick={handleClick}>
                {leftStat === null ? <div className="left-option">
                    {option1}
                </div> :
                <div className="left-option-container-nh">
                    <div className="left-option-nh">
                    {option1}
                    </div>
                    <p className="stat">{`${count}% `}</p>
                </div>}
            </div>
        </div>
     );
}
 
export default LeftSide;