import { UilMultiply } from '@iconscout/react-unicons'
import { useState } from 'react';
import axios from 'axios';
import Lottie from 'react-lottie-player'
import * as animationData from '../animations/animation_lkmri3yl.json'

const Modal = ({setModal}) => {

    const [name, setName] = useState('')
    const [idea, setIdea] = useState('')

    const [error, setError] = useState(null)
    const [success, setSuccessMsg] = useState(null)

    const handleIdeaClick = async (e) => {
        e.preventDefault()
        if(name === '' && idea === ''){
            setError('Please input a value for both name and idea.')
            setTimeout(() => {
                setError(null)
            }, 2000)
            
        }
        else if(name === '' && idea !== ''){
            setError('Please input a value for name.')
            setTimeout(() => {
                setError(null)
            }, 1700)
        }
        else if(name !== '' && idea === ''){
            setError('Please input a value for idea.')
            setTimeout(() => {
                setError(null)
            }, 1700)
        }
        else if(name !== '' && idea !== ''){
            try {
                await axios.post('https://localhost:3001/new_idea', {name, idea})
                setError(null)
                setSuccessMsg('Thanks for the idea! Duly noted :)')
                setTimeout(() => {
                    setSuccessMsg(null)
                    setName('')
                    setIdea('')
                    setModal(false)
                }, 1700)
            }
            catch(err){
                setError('Could not get idea. Try again later.')
                setTimeout(() => {
                    setError(null)
                    setSuccessMsg(null)
                    setName('')
                    setIdea('')
                    setModal(false)
                }, 2000)
            }
            
            
            
        }
    }


    return ( 
        <div>
            <div className="modal-container">
                <div className="modal-header">
                    <h4 className="modal-title">
                        Idea Jar 
                    </h4>
                    <UilMultiply className="modal-close-btn" size="20" color="#000000" onClick={() => setModal(false)}/>
                </div>
                <div className="modal-body">
                    <p>
                        Do you have a great topic idea for this website or any suggestions to help me improve it? Share your thoughts below!
                    </p>
                </div>
                {error && <div className='error-msg'>{error}</div>}
                {success && <div className='success-msg'>{success}</div>}
                <div className="modal-form-container">
                    <form className='modal-form' onSubmit={handleIdeaClick}>
                        <label htmlFor='name' className='name-lbl'>Name:</label>
                        <input type='text' id='name' value={name} name='name' placeholder='My name is...' onChange={(e) => setName(e.target.value)}/>
                        <label htmlFor='idea' className='idea-lbl'>Idea/Suggestion:</label>
                        <textarea type='text' id='idea' value={idea} name='idea' placeholder='Please add a question about...' onChange={(e) => setIdea(e.target.value)}/>
                        <div className='submit-btn-container'>
                            {!success ? <button className="submit-btn" type='submit'>SUBMIT</button> :
                            <Lottie
                            className='lottie-'
                            animationData={animationData}
                            play
                            loop={false}
                            style={{ width: 75, height: 75}}
                          />}
                        </div>
                    </form>
                </div>
            </div>
        </div>
     );
}
 
export default Modal;